.body {
  padding: 0;
  margin: 0;
  background-color: white;
  font-family: Arial, Helvetica, sans-serif;
}

.header {
  background-color: white;
  width: 100%;
}

.show-controls {
  margin: 0 auto;
  text-align: center;
}

.title-searcher {
  padding: 10px;
}

.btn-styles {
  background-color: darkmagenta;
  color: white;
  font-size: 14px;
  margin: 10px 0;
  height: 35px;
  padding: 0 14px;
}

.show-input {
  margin: 10px 0;
  padding: 4px;
  height: 22px;
  width: 300px;
  border-bottom: 2px solid black;
  font-size: 14px;
}

.show-result-item {
  display: inline-block;
  margin: 10px;
  text-align: center;
  border: 3px solid palevioletred;
  background-color: lightpink;
  width: 200px;
  height: 300px;
}

.show-result-item-active {
  border: 3px solid purple;
  background-color: violet;
}

.show-result-item img {
  width: 100%;
  height: 100%;
}

li {
  border: 3px solid palevioletred;
  background-color: lightpink;
  margin: 10px;
}
